import React from 'react';
import { Nav, NavDropdown, Navbar} from 'react-bootstrap';
import Logo from '../Logo/Logo';
import './Navigation.css'
import { dijkstra } from '../Pathfinding/algorithms/dijkstra.js';
//import { AStar } from '../Pathfinding/algorithms/AStar.js';
import { bfs } from '../Pathfinding/algorithms/BFS.js';
import { dfs } from '../Pathfinding/algorithms/DFS.js';


var speed = 15;
function changeSpeed(sp) {
    switch(sp) {
        case "slow":
            speed = 100;
            break;
        case "fast":
            speed = 5;
            break;
        case "average":
            speed = 50;
            break;
        default:
            speed = 15;
            break;
    }
}
function animate(visitedNodesInOrder, nodesInShortestPathOrder) {
    for (let i = 0; i <= visitedNodesInOrder.length; i++) {
        if (i === visitedNodesInOrder.length) {
            setTimeout(() => {
                animateShortestPath(nodesInShortestPathOrder);
            }, speed * i);
            return;
        }
        setTimeout(() => {
            const node = visitedNodesInOrder[i];
            const nodeClassName = document.getElementById(`node-${node.row}-${node.col}`,).className;
            if (nodeClassName !== 'node node-start' && nodeClassName !== 'node node-finish') {
                document.getElementById(`node-${node.row}-${node.col}`).className ='node node-visited';
            }
        }, speed * i);
    }
}
  
function animateShortestPath(nodesInShortestPathOrder) {
    for (let i = 0; i < nodesInShortestPathOrder.length; i++) {
        setTimeout(() => {
            const node = nodesInShortestPathOrder[i];
            const nodeClassName = document.getElementById(`node-${node.row}-${node.col}`,).className;
            if (nodeClassName !== 'node node-start' && nodeClassName !== 'node node-finish') {
                document.getElementById(`node-${node.row}-${node.col}`).className ='node node-shortest-path';
            }
        }, i * 40);
    }
}

var algo = "";
function changeAlgo(algorithm) {
    algo = algorithm;
}

function visualize(currentGrid, state, al) {
    clearGrid(currentGrid)
    const {grid} = currentGrid;
    const startNode = grid[state.startNodeRow][state.startNodeCol];
    const finishNode = grid[state.finishNodeRow][state.finishNodeCol];
    var visitedNodesInOrder= [];
    switch (al) {
        case "Dijkstra":
            visitedNodesInOrder = dijkstra(grid, startNode, finishNode);
            break;  
        /*case "AStar":
            visitedNodesInOrder = AStar(grid, startNode, finishNode);
             break;
            */
        case "BFS":
            visitedNodesInOrder = bfs(grid, startNode, finishNode);
            break;
        case "DFS":
            visitedNodesInOrder = dfs(grid, startNode, finishNode);
            break;
        default:
            // should never get here
            break;
    }    
    const nodesInShortestPathOrder = getNodesInShortestPathOrder(finishNode);
    animate(visitedNodesInOrder, nodesInShortestPathOrder);
}

function getNodesInShortestPathOrder(finishNode) {
    const nodesInShortestPathOrder = [];
    let currentNode = finishNode;
    while (currentNode !== null) {
      nodesInShortestPathOrder.unshift(currentNode);
      currentNode = currentNode.previousNode;
    }
    return nodesInShortestPathOrder;
}

function clearGrid(state) {
    const newGrid = state.grid.slice();
    for (const row of newGrid) {
        for (const node of row) {
            let nodeClassName = document.getElementById(`node-${node.row}-${node.col}`,).className;
            if (
                nodeClassName !== 'node node-start' &&
                nodeClassName !== 'node node-finish' &&
                nodeClassName !== 'node node-wall'
            ) {
                document.getElementById(`node-${node.row}-${node.col}`).className ='node';
                node.isVisited = false;
                node.distance = Infinity;
                node.distanceToFinishNode = Math.abs(state.finishNodeRow - node.row) + Math.abs(state.finishNodeCol - node.col);
            }
            if (nodeClassName === 'node node-finish') {
                node.isVisited = false;
                node.distance = Infinity;
                node.distanceToFinishNode = 0;
            }
            if (nodeClassName === 'node node-start') {
                node.isVisited = false;
                node.distance = Infinity;
                node.distanceToFinishNode = Math.abs(state.finishNodeRow - node.row) + Math.abs(state.finishNodeCol - node.col);
                node.isStart = true;
                node.isWall = false;
                node.previousNode = null;
                node.isNode = true;
            }
        }
    }
}


const Navigation = ({currentGrid, state}) => {
    return (
        <div>
            <Navbar id="nav" expand="sm" bg="dark" variant="dark">
                <Navbar.Brand href="#home">Pathfinding Visualizer
                <Logo/>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link onClick={() => clearGrid(state)}> Clear Board </Nav.Link>
                        {/*<Nav.Link  Add Random Wall </Nav.Link>
                        <Nav.Link> Clear Path </Nav.Link>*/}
                        <NavDropdown title="Algorithms" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#Dijkstra" onClick={() => changeAlgo("Dijkstra")} >Dijkstra's Algorithm</NavDropdown.Item>
                            {/*<NavDropdown.Item href="#AStar" onClick={() => changeAlgo("AStar")} >A* Algorithm </NavDropdown.Item>*/}
                            <NavDropdown.Item href="#BFS" onClick={() => changeAlgo("BFS")} >Breath-First Search</NavDropdown.Item>
                            <NavDropdown.Item href="#DFS" onClick={() => changeAlgo("DFS")}>Depth-First Search</NavDropdown.Item>
                            {/*<NavDropdown.Item ">Gready</NavDropdown.Item>
                            */}
                        </NavDropdown>
                        <NavDropdown title="Speed" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#slow" onClick={() => changeSpeed("slow")}>Slow</NavDropdown.Item>
                            <NavDropdown.Item href="#average" onClick={() => changeSpeed("average")}>Average</NavDropdown.Item>
                            <NavDropdown.Item href="#fast" onClick={() => changeSpeed("fast")}>Fast</NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="#visualize" bg="primary" variant="primary" onClick={() => visualize(currentGrid, state, algo)}> Visualize </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    );
}

export default Navigation;