import React, {Component} from 'react';
import Node from './Node/Node';
import Navigation from '../Navigation/Navigation';

import './Pathfinding.css';

const START_NODE_ROW = 15;
const START_NODE_COL = 5;
const FINISH_NODE_ROW = 5;
const FINISH_NODE_COL = 35;
const MAX_ROW = 20;
const MAX_COLUMN = 50;

const getInitialGrid = () => {
    const grid = [];
    for (let row = 0; row < MAX_ROW; row++) {
        const currentRow = [];
        for (let col = 0; col < MAX_COLUMN; col++) {
            currentRow.push(createNode(col, row));
        }
        grid.push(currentRow);
    }
    return grid;
};
  
const createNode = (col, row) => {
    return {
        col,
        row,
        isStart: row === START_NODE_ROW && col === START_NODE_COL,
        isFinish: row === FINISH_NODE_ROW && col === FINISH_NODE_COL,
        distance: Infinity,
        distanceToFinishNode: Math.abs(FINISH_NODE_ROW - row) + Math.abs(FINISH_NODE_COL - col),
        isVisited: false,
        isWall: false,
        previousNode: null,
    };
};
  
const getNewGridWithWallToggled = (grid, row, col) => {
    const newGrid = grid.slice();
    const node = newGrid[row][col];
    const newNode = {
        ...node,
        isWall: !node.isWall,
    };
    newGrid[row][col] = newNode;
    return newGrid;
};
  
class Pathfinding extends Component {
    constructor() {
        super();
        this.state = {
            grid:[],
            mouseIsPressed:false,
            startNodeCol: START_NODE_COL,
            startNodeRow: START_NODE_ROW,
            finishNodeCol: FINISH_NODE_COL,
            finishNodeRow: FINISH_NODE_ROW,
        }
    } 
    
    componentDidMount() {
        const grid = getInitialGrid();
        this.setState({grid});
    }
    
    handleMouseDown(row, col) {
        const newGrid = getNewGridWithWallToggled(this.state.grid, row, col);
        this.setState({grid: newGrid, mouseIsPressed: true});
    }
    
    handleMouseEnter(row, col) {
        if (!this.state.mouseIsPressed) return;
        const newGrid = getNewGridWithWallToggled(this.state.grid, row, col);
        this.setState({grid: newGrid});
    }
    
    handleMouseUp() {
        this.setState({mouseIsPressed: false});
    }



    render() {
        const {grid, mouseIsPressed} = this.state;
        return (
            <div>
                <Navigation currentGrid={{grid}} state={this.state}/>
                <div className="grid">
                    {grid.map((row, rowIdx) => {
                    return (
                        <div key={rowIdx}>
                        {row.map((node, nodeIdx) => {const {row, col, isFinish, isStart, isWall} = node;
                            return (
                                <Node
                                key={nodeIdx}
                                col={col}
                                isFinish={isFinish}
                                isStart={isStart}
                                isWall={isWall}
                                mouseIsPressed={mouseIsPressed}
                                onMouseDown={(row, col) => this.handleMouseDown(row, col)}
                                onMouseEnter={(row, col) => this.handleMouseEnter(row, col)}
                                onMouseUp={() => this.handleMouseUp()}
                                row={row}></Node>
                            );
                        })}
                        </div>
                    );
                })}
                </div>
            </div>
        );
      }
    
}

export default Pathfinding;